import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Datatop } from '../datatop';
import { DatatopService } from '../datatop.service';

@Component({
  selector: 'app-hero-detail',
  templateUrl: './hero-detail.component.html',
  styleUrls: ['./hero-detail.component.css']
})
export class HeroDetailComponent implements OnInit {
  data :Datatop;
  oriData: Datatop;
  getId:string;

  constructor(
    private route: ActivatedRoute,
    private dataService: DatatopService
  ) { }

  ngOnInit() {
    // console.log(this.route.snapshot.paramMap.get('id'));
    this.getId = this.route.snapshot.paramMap.get('id');
    const arrData = this.dataService.getDataTop();
    this.data = arrData.find(i => i.id === parseInt(this.getId));
    var name = this.data.name;
    var id = this.data.id;
    this.oriData = {
      name: name,
      id: id
    }

  }
  // clickBack(){
  //   this.dataService.oriData(this.oriData,parseInt(this.getId));
  // }
  clickSave(){
    this.oriData.id = Number(this.oriData.id);
    console.log(this.oriData);
    this.dataService.saveData(this.oriData,parseInt(this.getId));
  }

}
