import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, FormArray, Validators } from '../../../../node_modules/@angular/forms';
import { YeuCauModel } from '../yeu-cau-model';
import { DatatopService } from '../../datatop.service';
import { PatientModel } from '../patient-model';

@Component({
  selector: 'app-request-record',
  templateUrl: './request-record.component.html',
  styleUrls: ['./request-record.component.scss']
})
export class RequestRecordComponent implements OnInit {
  errArr = [];
  TnF = true;
  yeuCauFormGroup: FormGroup;
  get servieFA(): FormArray {
    return this.yeuCauFormGroup.get('service') as FormArray;
  };

  errorMess = {
    name: {
      required: 'name la bat buoc',
    },
    sinceTime: {
      required: 'Thoi gian la bat buoc'
    },
    place: {
      required: 'Noi kham la bat buoc'
    }
  };

  constructor(
    private fb: FormBuilder,
    private dataService: DatatopService
  ) { }

  ngOnInit() {
    this.yeuCauFormGroup = this.fb.group({
      idPatient: this.dataService.findID() + 1,
      name: ['', [Validators.required]],
      status: 'Mới',
      sinceTime: ['', [Validators.required]],
      place: ['', [Validators.required]],
      service: this.fb.array([
        this.fb.control('')
      ]),
      security: '',
    });
    // this.addYeuCau();
    this.yeuCauFormGroup.valueChanges.subscribe(success => {
      console.log('Thanh cong', success);
      this.getFormErr(success);

    }, err => {
      console.log('That bai', err);
    });
  }
  getFormErr(formValue) {
    this.errArr = [];
    // console.log('success ',formValue);
    for (const control in formValue) {
      console.log('control: ', control);
      const formControl = this.yeuCauFormGroup.get(control);
      if (formControl.errors) {
        // tslint:disable-next-line:forin
        console.log('formcontrol:', formControl);
        for (const errControlName in formControl.errors) {
          this.errArr.push(this.errorMess[control][errControlName]);
        }
      }
    }
    console.log('err: ', this.errArr);
    if(this.errArr.length == 0) this.TnF = false;
    else this.TnF = true;
    console.log('tnf: ',this.TnF);
  }

  addYeuCau() {
    this.servieFA.push(this.fb.control(''));
  };

  submitForm() {
      const patientObj = new PatientModel();
      const yeuCauObj = this.yeuCauFormGroup.value as YeuCauModel;
      patientObj.yeuCau = yeuCauObj;
      console.log('patientObj', patientObj);
      this.dataService.addPatient(patientObj);
  }
  DelItem(i) {
    this.servieFA.removeAt(i);
  }
}
