import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '../../../../node_modules/@angular/router';
import { FormBuilder, FormGroup, FormArray, Validators } from '../../../../node_modules/@angular/forms';
import { DatatopService } from '../../datatop.service';
import { PatientModel } from '../patient-model';
import { LichKhamModel } from '../lich-kham-model';

@Component({
  selector: 'app-plan-record',
  templateUrl: './plan-record.component.html',
  styleUrls: ['./plan-record.component.scss']
})
export class PlanRecordComponent implements OnInit {
  getId: string;
  data: PatientModel;
  lichKham: FormGroup;
  numOfPerson: number;
  formControlInPerson: FormGroup;

  errArr = [];

  errorMess = {
    fullName: { required: 'ten la bat buoc' },
    birthday: { required: 'ngay sinh la bat buoc' },
    sex: { required: 'Giới tính là bắt buộc' },
    email: { required: 'Mail là bắt buộc' },
    phone: { required: 'Sdt la bat buoc' },
    appointment: { required: 'Lich hen la bat buoc' }
  };

  get personFA(): FormArray {
    return this.lichKham.get('person') as FormArray;
  };

  serviceFA(i: number): FormArray {
    return this.personFA.at(i).get('service') as FormArray;
  };


  constructor(
    private fb: FormBuilder,
    private dataService: DatatopService,
    private route: ActivatedRoute,
  ) { }

  ngOnInit() {
    this.getId = this.route.snapshot.paramMap.get('id');
    // console.log('id: ', this.getId);
    const patientsArr = this.dataService.getDataPatients();
    this.data = patientsArr.find(i => Number(i.yeuCau.idPatient) == Number(this.getId));
    // if (this.data.lichKham == null) {
    this.numOfPerson = Number(this.data.hopDong.numOfPerson);
    console.log('data:', this.data);
    this.lichKham = this.fb.group({
      id: this.dataService.findIDofLK() + 1,
      status: 'Mới',
      person: this.fb.array([
        this.fb.group({
          fullName: ['', Validators.required],
          birthday: ['', Validators.required],
          add: '',
          sex: ['', Validators.required],
          email: ['', Validators.required],
          phone: ['', Validators.required],
          appointment: ['', Validators.required],
          service: this.fb.array([
            this.fb.group({
              item: '',
              quantity: ''
            })
          ])
        })
      ]),
    });
    for (let i = 0; i < this.numOfPerson - 1; i++) {
      this.addPerson();
    }
    console.log('day:', this.personFA);
    console.log('day:', this.serviceFA(0));
  }
  initPerson() {
    return this.fb.group({
      fullName: ['', Validators.required],
      birthday: ['', Validators.required],
      add: '',
      sex: ['', Validators.required],
      email: ['', Validators.required],
      phone: ['', Validators.required],
      appointment: ['', Validators.required],
      service: this.fb.array([
        this.fb.group({
          item: '',
          quantity: ''
        })
      ])
    })
  }
  addPerson() {
    const control = this.lichKham.get('person') as FormArray;
    control.push(this.initPerson());
  }

  initService() {
    return this.fb.group({
      item: '',
      quantity: ''
    })
  }
  addService(i: number) {
    // const control = this.lichKham.get('person') as FormArray;
    // const control2 = control.at(i).get('service') as FormArray;
    // console.log('addservice: ',this.serviceFA(i));
    const control = this.serviceFA(i);
    control.push(this.initService());
    console.log('addservice: ', this.serviceFA(i));
  }

  getFormErr(formControl: FormGroup, formValue) {
    this.errArr = [];
    // console.log('success ',formValue);
    for (const control in formValue) {
      // console.log('control: ', control);
      const formControl1 = formControl.get(control);
      if (formControl1.errors) {
        // tslint:disable-next-line:forin
        // console.log('formcontrol1:', formControl1);
        for (const errControlName in formControl1.errors) {
          this.errArr.push(this.errorMess[control][errControlName]);
        }
      }
    }
    // console.log('err: ', this.errArr);
    // if (this.errArr.length == 0) this.TnF = false;
    // else this.TnF = true;
    // console.log('tnf: ', this.TnF);
  }

  submitForm() {
    const lichKhamObj = this.lichKham.value as LichKhamModel;
    this.data.lichKham = lichKhamObj;
    console.log('lichkham: ', lichKhamObj);
  }

  delService(i, i2) {
    this.serviceFA(i).removeAt(i2);
  }

  delPerson(i) {
    this.personFA.removeAt(i);
  }
}
