import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '../../../../node_modules/@angular/router';
import { DatatopService } from '../../datatop.service';
import { PatientModel } from '../patient-model';
import { FormGroup, FormBuilder, FormArray } from '../../../../node_modules/@angular/forms';
import { KetQuaModel } from '../ket-qua-model';

@Component({
  selector: 'app-result-record',
  templateUrl: './result-record.component.html',
  styleUrls: ['./result-record.component.scss']
})
export class ResultRecordComponent implements OnInit {
  getId: string;
  data: PatientModel;
  ketQua: FormGroup;
  get resultFA(): FormArray{
    return this.ketQua.get('result') as FormArray;
  };
  constructor(
    private route: ActivatedRoute,
    private dataService: DatatopService,
    private fb: FormBuilder,
  ) { }

  ngOnInit() {
    this.getId = this.route.snapshot.paramMap.get('id');
    // console.log('id: ',this.getId);
    const patientsArr = this.dataService.getDataPatients();
    this.data = patientsArr.find(i => Number(i.yeuCau.idPatient) == Number(this.getId));
    // console.log('data:', this.data);
    this.ketQua = this.fb.group({
      result: this.fb.array([])
    });
    this.addMoreResult();
  }

  addMoreResult() {
    const formControl = this.fb.group({
      item:''
    });
    this.resultFA.push(formControl);
  };
  delItem(i){
    this.resultFA.removeAt(i);
  }
  submitForm() {
    // const patientObj = new PatientModel();
    const ketQuaObj = this.ketQua.value as KetQuaModel;
    this.data.ketQua = ketQuaObj;
    console.log('ketqua data: ',this.data);
    // this.data.lichKham.status = 'Đang khám';
  }

}
