import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes, RoutesRecognized } from '@angular/router';
import { TopheroesComponent } from './topheroes/topheroes.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { HeroDetailComponent } from './hero-detail/hero-detail.component';
import { TrackingRecordComponent } from './model/tracking-record/tracking-record.component';
import { RequestRecordComponent } from './model/request-record/request-record.component';
import { ViewRecordComponent } from './model/view-record/view-record.component';
import { ContractRecordComponent } from './model/contract-record/contract-record.component';
import { PlanRecordComponent } from './model/plan-record/plan-record.component';
import { ResultRecordComponent } from './model/result-record/result-record.component';


const routes: Routes = [
  {
    path: 'topheroes',
    component: TopheroesComponent
  },
  {
    path: 'dashboard',
    component: DashboardComponent
  },
  {
    path: '',
    redirectTo: 'tracking-record',
    pathMatch: 'full'
  },
  {
    path: 'detail/:id',
    component: HeroDetailComponent
  },
  {
    path: 'tracking-record',
    component: TrackingRecordComponent
  },
  {
    path: 'request-record',
    component: RequestRecordComponent
  },
  {
    path: 'view-record',
    component: ViewRecordComponent
  },
  {
    path: 'addContract/:id',
    component: ContractRecordComponent
  },
  {
    path: 'addPlan/:id',
    component: PlanRecordComponent
  },
  {
    path: 'addResult/:id',
    component: ResultRecordComponent
  }
];

@NgModule({
  imports: [
    CommonModule,
    RouterModule.forRoot(routes),
  ],

  exports: [RouterModule],

  declarations: []
})
export class AppRoutingModule { }
