import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '../../../../node_modules/@angular/forms';
import { PatientModel } from '../patient-model';
import { HopDongModel } from '../hop-dong-model';
import { DatatopService } from '../../datatop.service';
import { ActivatedRoute } from '../../../../node_modules/@angular/router';

@Component({
  selector: 'app-contract-record',
  templateUrl: './contract-record.component.html',
  styleUrls: ['./contract-record.component.scss']
})
export class ContractRecordComponent implements OnInit {
  getId: string;
  data: PatientModel;
  hopDong: FormGroup;
  errArr =[];
  TnF = true;
  tienConLai = 0;

  errorMess = {
    amountOfMoney: {
      required: 'Tong tien la bat buoc',
    },
    deposit: {
      required: 'Dat coc la bat buoc'
    },
    depositDate: {
      required: 'Ngay dat coc la bat buoc'
    },
    methodOfPay: {
      required: 'Hinh thuc tra tien'
    },
    deadline: {
      required: 'Han cuo la bat buoc'
    },
    numOfPerson: {
      required: 'So nguoi kham la bat buoc'
    }
  };

  constructor(
    private fb: FormBuilder,
    private dataService: DatatopService,
    private route: ActivatedRoute,
  ) { }

  ngOnInit() {
    this.getId = this.route.snapshot.paramMap.get('id');
    // console.log('id: ',this.getId);
    const patientsArr = this.dataService.getDataPatients();
    this.data = patientsArr.find(i => Number(i.yeuCau.idPatient) == Number(this.getId));
    console.log('data:',this.data);
    this.hopDong = this.fb.group({
      id: this.dataService.findIDofHD() + 1,
      amountOfMoney: ['',[Validators.required]],
      deposit:['', [Validators.required]],
      remainingAmount: '',
      depositDate: ['',[Validators.required]],
      deadline: ['',[Validators.required]],
      methodOfPay: ['',[Validators.required]],
      status: 'Đăt cọc',
      numOfPerson: ['', Validators.required]
    });
    // this.tienConLai = Number(this.hopDong.get('amountOfMoney').value) - Number(this.hopDong.get('deposit').value);
    // this.hopDong.get('remainingAmount').patchValue(this.tienConLai);
    // console.log('tienthua: ',this.hopDong.get('remainingAmount').value);
    // console.log('typeof: ',typeof Number(this.hopDong.get('amountOfMoney')));
    this.hopDong.valueChanges.subscribe(success => {
      console.log('Thanh cong', success);
      this.getFormErr(success);

    }, err => {
      console.log('That bai', err);
    });
  }
  getFormErr(formValue) {
    this.errArr = [];
    // console.log('success ',formValue);
    for (const control in formValue) {
      console.log('control: ', control);
      const formControl = this.hopDong.get(control);
      if (formControl.errors) {
        // tslint:disable-next-line:forin
        console.log('formcontrol:', formControl);
        for (const errControlName in formControl.errors) {
          this.errArr.push(this.errorMess[control][errControlName]);
        }
      }
    }
    console.log('err: ', this.errArr);
    if(this.errArr.length == 0) this.TnF = false;
    else this.TnF = true;
    console.log('tnf: ',this.TnF);
  }

  submitForm() {
    // const patientObj = new PatientModel();
    const hopDongObj = this.hopDong.value as HopDongModel;
    this.data.hopDong = hopDongObj;
    this.data.yeuCau.status = 'Xong';
    // console.log('datalist: ',);
    // this.dataService.addPatient(patientObj);
  }

}
