import { Component, OnInit } from '@angular/core';
import { DatatopService } from '../../datatop.service';
import { TotalModel } from '../total-model';
import { PatientModel } from '../patient-model';

@Component({
  selector: 'app-tracking-record',
  templateUrl: './tracking-record.component.html',
  styleUrls: ['./tracking-record.component.scss']
})
export class TrackingRecordComponent implements OnInit {
  // dataPatients: TotalModel[]; /* No create some block,just display block  */
  dataPatients: PatientModel[];
  mode = 1;

  constructor(
    private listPatientService: DatatopService
  ) { }

  ngOnInit() {
    this.dataPatients = this.listPatientService.getDataPatients();
 
    // console.log('data: ',typeof this.dataPatients[0].ketQua);
    
  }

  checkNotEmpty(checkObject, index){
    // return Object.keys(checkObject).length;
    return (checkObject != null);
  }

  adminMode(){
    this.mode = 1;
  }

  guestMode(){
    this.mode = 0;
  }

}
